<template>
    <div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="id"
            :loading="loading"
            disable-sort
            hide-default-footer
        >
            <!-- eslint-disable  -->
            <template v-slot:item.ip_code="{item}">
                <div class="d-flex">
                    <span class="text-black">#{{ item.ip_code }}</span>
                </div>
            </template>
            
            <template v-slot:item.ip_title="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.ip_title }}</span>
                </div>
            </template>

            <template v-slot:item.ip_mass="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.ip_mass) }} kg</span>
                </div>
            </template>
            
            <template v-slot:item.ip_return="{item}">
                <a class="d-flex align-center justify-center" @click="OpenTransferDialog(item)"
                v-if="$can('read', 'outsource_mato_toquvxona_skladi_otkazish')">
                    <img class="text-blue img-width-20px mr-1" style="margin-bottom: 5px;"
                        src="@/assets/icons/export2.svg" alt="export">
                    <span class="text-blue">{{ item.ip_return }}</span>
                </a>
            </template>
<!-- 
            <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'outsource_mato_toquvxona_skladi')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                    </div>
                    <div class="cursor-pointer mt-1" v-if="$can('delete', 'outsource_mato_toquvxona_skladi')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div>
                </div>
            </template> -->
            
            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>
            
        </v-data-table>
        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>


        <IpTransfer v-if="$can('read', 'outsource_mato_toquvxona_skladi_otkazish')" :ipTransferDialog="ipTransferDialog" :itemData="itemData"></IpTransfer>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import NoData from "@/components/base/NoData.vue";
import IpTransfer from '@/components/modals/Outsource/Transfer.vue'
// import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
// import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin],
    components: {
        IpTransfer,
        // DeleteModal,
        NoData
    },
    data() {
        return {
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            ipTransferDialog: false,
            loading: false,
            itemData: {},
            headers: [
                {text: 'Berilgan ip kodi', value: 'ip_code', align: 'start', class: 'table-header'},
                {text: 'Berilgan ip nomi', value: 'ip_title', align: 'center', class: 'table-header'},
                {text: 'Ip massasi', value: 'ip_mass', align: 'center', class: 'table-header'},
                {text: 'Ipni qaytarish', value: 'ip_return', align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: []
        }
    },
    async mounted(){
        await this.refresh()

        if(!this.$can('read', 'outsource_mato_toquvxona_skladi_otkazish')){
            this.headers.pop();
        }
    },
    watch:{
        '$route.params.id': {
            handler: async function(value) {
                await this.refresh()
            },
            deep: true,
            immediate: true
        },
        '$store.state.main.outsource_material_warehouse': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_OUTSOURCE_MATERIAL_WAREHOUSE', false)
                }
            },
            deep: true,
            immediate: true
        },
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
    },
    computed: {
        ...mapState({
            factory_threads: state => state.outsource.factory_threads,
            searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
        }),
    },
    methods:{
        ...mapActions({
            factoryThreads: 'outsource/factoryThreads'
        }),
        async refresh(){
            this.loading = true
            await this.factoryThreads({ page: this.page, product_case: 'raw', 
            factory: this.$route.params.id, search: this.searchValue, date: this.dateFilterValue })
            this.page = this.factory_threads.current_page;
            this.pageCount = Math.ceil(this.factory_threads.total / 10);
            
            this.items = this.factory_threads.data
            this.items.forEach((e) => {
                e.ip_code = e.product.code;
                e.ip_title = e.product.name;
                e.ip_mass = e.amount;
                e.ip_return = 'Otkazish';
            })
            this.loading = false
        },
        OpenTransferDialog(item){
            this.itemData = item.product
            this.ipTransferDialog = true
        },
        closeEditModal(){
            this.editModal = false
        }
        
    }
}
</script>

