<template>
    <div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="id"
            :loading="loading"
            disable-sort
            hide-default-footer
        >
            <!-- eslint-disable  -->
            <template v-slot:item.direction="{item}">
                <div class="d-flex">
                    <span class="text-black">{{ item.direction }}</span>
                </div>
            </template>

            <template v-slot:item.amount="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.amount) }} kg</span>
                </div>
            </template>


            <template v-slot:item.date="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.date }}</span>
                </div>
            </template>

            <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" 
                    @click="(product = item) && (editModal = true)"
                    v-if="$can('update', 'outsource_mato_kirim')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                    </div>
                    <div class="cursor-pointer mt-1" 
                    @click="(element = item) && (deleteModal = true)"
                    v-if="$can('delete', 'outsource_mato_kirim')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div>
                </div>
            </template>

            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>

        </v-data-table>
        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>



        <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditDialog">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                
                <ValidationObserver ref="formEditOutcomeThread" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                      
                        <div class="mt-5">
                            <span class="text-black ml-1">Massa</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'amount']}"
                              v-slot="{ errors }">
                                <v-text-field outlined :value="formatPrice(product.amount)"  class="mt-2"
                                    @input="formatPrice2($event, 'amount')"
                                    placeholder="Misol: 10 kg" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditDialog"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>



        <DeleteModal 
        :deleteModal="deleteModal" 
        :user_id="element.id" 
        :actionName="'updateDelete/deleteFactoryIncome'"
        :modalTitle="'Ip ochirilishi'"
        :modalDesc="'Bu ipni ochirmoqchimisiz?'"></DeleteModal>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import NoData from "@/components/base/NoData.vue";
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin, loaderMixin],
    components:{
        DeleteModal,
        NoData
    },
    data() {
        return {
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            headers: [
                // {text: 'Status', value: 'status'},
                {text: 'Olindi', value: 'direction', align: 'start', class: 'table-header'},
                {text: 'Massa', value: 'amount', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
            element: {},
            product: {
                amount: '',
            },
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
            
        }
    },
    watch: {
        '$route.params.id': {
            handler: async function(value) {
                await this.refresh()
            },
            deep: true,
            immediate: true
        },
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.outsource_material_income': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_OUTSOURCE_MATERIAL_INCOME', false)
                }
            },
            deep: true,
            immediate: true
        },
    },

    async mounted() {
        await this.refresh();

        if(!this.$can('update', 'outsource_mato_kirim') && !this.$can('delete', 'outsource_mato_kirim')){
            this.headers.pop();
        }
    },
    computed: {
        ...mapState(
            {
                outcome_ips: state => state.warehouse.outcome_ips,
                searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
            }
        ),
    },
    methods: {
        ...mapActions({
            fetchOutcome: 'warehouse/fetchOutcome',
            updateFactoryIncome: 'updateDelete/updateFactoryIncome',
            deleteFactoryIncome: 'updateDelete/deleteFactoryIncome'
        }),
        async refresh() {
            this.loading = true
            await this.fetchOutcome({page: this.page, factory_id: this.$route.params.id,
            search: this.searchValue, date: this.dateFilterValue});
            this.page = this.outcome_ips.current_page;
            this.pageCount = Math.ceil(this.outcome_ips.total / 10);
            this.items = this.outcome_ips.data;

            this.items.forEach((elem) => {
                elem.direction = elem.product.name + ' - #' + elem.product.code;
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        




        // ============= PRICE_FORMATTER START =============
        formatPrice2(value, input) {
            if(value != ''){
                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.product.amount = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============










        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.product = {
                amount: '',
            }
            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeEditDialog(){
            this.clearInputs();
            this.editModal = false;
            this.$refs.formEditOutcomeThread.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============















        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formEditOutcomeThread.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= EDITING_IP START =============
                    let ip = await this.updateFactoryIncome(
                        { 
                            id: this.product.id,
                            factory_product_case: 'raw',
                            storage_product_case: 'raw',
                            storage_type_id: 1,
                            amount: parseFloat(this.product.amount.toString().replace(/,/g, '')),
                        })

                    if(ip.response && ip.response.status == 422){
                        if(ip.response.data.errors.amount){
                            this.errorMessage = ip.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(ip.response && ip.response.status == 409){
                        if(ip.response.data.message){
                            this.errorMessage = ip.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli tahrirlandi!'
                        this.$toast.success(toastTitle);
                        this.closeEditDialog();
                        this.refresh()
                        // this.$store.commit('main/SET_UPDATE_MAVJUD_IPS', true)
                    }
                    // ============= EDITING_IP END =============
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })
        }
        // ============= ON_SUBMIT END =============
    }
}
</script>

