<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="tookDialog"
        >
            <template>
                <v-card class="ip_add">

                    <div class="ip_add__title">
                        <span class="text-black">Mahsulot olinmoqda</span>
                        <v-btn icon @click="closeOrderDialog">
                            <img src="@/assets/images/BackButton.svg" alt="BackButton">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOutsourceOrder" v-slot="{ handleSubmit, invalid }">
                        <form class="ip_add__content" @submit.prevent="handleSubmit(onSubmit)">

                            <div class="mt-3">
                                <span class="text-black ml-1">Sana</span>
                                <v-menu
                                    v-model="dateMenu"
                                    :close-on-content-click="false"
                                    :nudge-right="40"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-text-field
                                                class="mt-2"
                                                placeholder="Tanlang"
                                                v-model="dateFormatted"
                                                readonly
                                                outlined
                                                v-bind="attrs"
                                                v-on="on"
                                                @blur="date = parseDate(dateFormatted)"
                                                :error-messages="errors"
                                            ></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-date-picker
                                        color="blue lighten-1"
                                        v-model="date"
                                        @input="dateMenu = false"
                                        locale="ru-ru"
                                        first-day-of-week="1"
                                        :error-messages="errors"
                                    ></v-date-picker>
                                    </ValidationProvider>
                                </v-menu>
                            </div>

                            <div>
                                <span class="text-black ml-1">Mahsulotni tanlash</span>
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-text-field append-icon="mdi-chevron-right"
                                        label="Tanlang" outlined class="mt-2"
                                        :value="existingMato.product.name"
                                        :error-messages="errors">
                                        <!-- @click="productSelectDialog = true" -->
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">Massa (kg)</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'amount']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors" 
                                        @input="formatPrice(product.amount, 'amount')"
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        v-model="product.amount" min="1"
                                        outlined placeholder="Misol: 1,200 kg" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">IP xarajati</span>
                                <div class="d-flex" v-for="(item, index) in ips" :key="index">

                                    <ValidationProvider :rules="{required: true}"
                                    v-slot="{ errors }" class="d-flex align-top w-75">
                                        <v-select :items="existingIps" item-text="name" item-value="id" 
                                            label="Ipni tanlang" outlined class="mt-2"
                                            v-model="item.id" :error-messages="errors">
                                        </v-select>
                                    </ValidationProvider>

                                    <ValidationProvider :rules="{required: true}"
                                    v-slot="{ errors }" class="d-flex align-top w-35">
                                        <v-text-field :error-messages="errors" 
                                            v-model="item.percentage" min="1" max="100"
                                            onkeypress="return /[0-9,/.]/i.test(event.key)"
                                            outlined suffix="%" class="mt-2 ml-2">
                                        </v-text-field>
                                    </ValidationProvider>

                                    <v-btn icon @click="removeInput(index)" class="mt-3 ml-2">
                                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                                    </v-btn>
                                    
                                </div>
                            </div>

                            <div>
                                <button class="btn-new-white mb-5" type="button" @click="addOneIp">
                                    <img src="@/assets/icons/add-circle-blue.svg"
                                     class="mr-1" alt="add-blue" style="width: 20px;">
                                    <span class="text-blue">Ip qo`shish</span>
                                </button>
                            </div>

                            <div>
                                <div class="d-flex">
                                    <div class="d-flex align-top w-75">
                                        <v-text-field value="Isporeniya" outlined class="mt-2" readonly>
                                        </v-text-field>
                                    </div>
                                    
                                    <ValidationProvider :rules="{required: true}"
                                    v-slot="{ errors }" class="d-flex align-top w-35">
                                        <v-text-field :error-messages="errors" 
                                            v-model="product.evaporation" min="1" max="100"
                                            onkeypress="return /[0-9,/.]/i.test(event.key)"
                                            outlined suffix="%" class="mt-2 ml-2">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>
                            </div>


                            <div>
                                <span class="text-black ml-1">Umumiy Ip xarajati</span>
                                <div class="d-flex" v-for="(elem, index2) in ips" :key="index2">
                                    <v-text-field outlined class="d-flex align-top w-75 mt-2"
                                    :value="productInfo(elem)" readonly>
                                    </v-text-field>
                                
                                    <v-text-field readonly suffix="kg"
                                        :value="productAmount(elem)" min="1"
                                        outlined class="d-flex align-top w-35 mt-2 ml-2">
                                    </v-text-field>
                                </div>




                                <div class="d-flex border-top">
                                    <v-text-field value="Isporeniya" outlined
                                     class="mt-5 d-flex align-top w-75" readonly>
                                    </v-text-field>

                                    <v-text-field readonly
                                        :value="productEvaporation()" min="1"
                                        outlined suffix="kg" class="d-flex align-top w-35 mt-5 ml-2">
                                    </v-text-field>
                                </div>


                            </div>


                            <div>
                                <span class="text-black ml-1">Berilgan summa</span>
                                <ValidationProvider :rules="{error: [errorStatus, errorMessage, errorPlace, 'paid']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        outlined min="1" @input="formatPrice(product.paid, 'paid')"
                                        placeholder="Misol: 1,000,000 som"
                                        class="mt-2" v-model="product.paid">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    class="backBtn btn"
                                    elevation="0"
                                    @click="closeOrderDialog"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn btn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>

                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
     
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: ['tookDialog', 'item'],
    mixins: [dateMixin, loaderMixin],
    components: {
        PerfectScrollbar
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            productSelectDialog: false,
            existingProductType: '',

            ips: [
                {id: '', percentage: ''}
            ],

            product: {
                amount: 0,
                paid: 0,
                evaporation: ''
            },
            existingMatoSearch: '',
            existingMatos: [],
            existingMato: {product: ''},

            existingIps: [],
            // ============= SECONDARY_DATA =============






            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        'item': {
            handler(newValue, oldValue){
                this.existingMato = newValue
            }, deep: true
        },
        // ============= CLEAR_ERRORS START =============
        'product': {
            handler(newValue){
                if(newValue){
                    this.clearErrors()
                }
            }, deep: true
        },
        existingMato(newValue, oldValue){
            if(newValue != ''){
                this.clearErrors()
            }
        },
        async tookDialog(newValue, oldValue){
            if(newValue){
                await this.refresh()
            }
        }, 
        // ============= CLEAR_ERRORS END =============












        // ============= OTHER_WATCHERS START =============
        async existingMatoSearch(newValue, oldValue) {
            clearTimeout(this._timerId)

            this._timerId = setTimeout(async () => {
                if(newValue){
                    await this.fetchOrders({type: 'material', factory: this.$route.params.id, status: 'in_process', search: newValue})
                }else if(newValue == ''){
                    await this.fetchOrders({type: 'material', factory: this.$route.params.id, status: 'in_process'})
                }
                this.existingMatos = this.orders.data;
            }, 500)
        },
        'product.evaporation':{
            handler(newValue, oldValue){
                if(newValue > 100){
                    this.$toast.error('Isporeniya 100% dan oshmasligi zarur!')
                }
            }, deep: true
        },
        'ips':{
            handler(newValue, oldValue){
                if(newValue){
                    newValue.forEach((element) => {
                        element.item = this.existingIps.find((e) => {
                            return e.id == element.id
                        })
                    })


                    let total = 0;
                    newValue.forEach((element) => {
                        total = total + parseFloat(element.percentage)
                        if(total > 100){
                            this.$toast.error('Ip xarajatlari umumiy 100% dan oshmasligi zarur!')
                        }
                    })
                }
            }, deep: true
        },  
        // ============= OTHER_WATCHERS END =============
    },
    async mounted(){
        await this.refresh2()
    },
    computed: {
        ...mapState(
            {
                factory_threads: state => state.outsource.factory_threads,
                orders: state => state.outsource.orders
            }
        ),
    },
    methods:{
        ...mapActions(
            {
                postOrderProduct: 'outsource/postOrderProduct',
                factoryThreads: 'outsource/factoryThreads',
                fetchOrders: 'outsource/fetchOrders'
            }
        ),






        // ============= ADD_AND_REMOVE_INPUT START =============
        addOneIp(){
            if(this.ips.length < 10){
                this.ips.push({id: '', percentage: ''})
            }else{
                this.$toast.error('Iplarning soni 10tadan oshishi mumkin emas!');
            }
        },
        removeInput(index){
            if(this.ips.length != 1){
                this.ips.splice(index, 1);
            }else{
                this.$toast.error('Maydonlarning soni bittadan kam bo`lishi mumkin emas!');
            }
        },
        // ============= ADD_AND_REMOVE_INPUT END =============


     
     









        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){

                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'paid'){
                    this.product.paid = currency
                }else if(input == 'amount'){
                    this.product.amount = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============






        









        // ============= CALCULATION_FUNCTIONS START =============
        productInfo(item){
            let result;
            if(item.item && item.item.product){
                result = item.item.product.name + ' - #' + item.item.product.code
            }
            return result
        },

        productAmount(item){
            let first_step, result;
            if(item.item && item.item.product){
                let amount = this.product.amount.toString().replace(/,/g, '');
                first_step = (amount * item.percentage) / 100;
                result = first_step + ( first_step * (this.product.evaporation/100))
            }
            return result
        },
        productAmount2(item){
            console.log(item)
            let first_step;
            if(item.item && item.item.product){
                let amount = this.product.amount.toString().replace(/,/g, '');
                first_step = (amount * item.percentage) / 100;
            }
            return first_step
        },
        productEvaporation(){
            let result, totalWaste = 0;
            this.ips.forEach((elem) => {
                totalWaste = totalWaste + this.productAmount(elem)
            })
            let amount = this.product.amount.toString().replace(/,/g, '');

            result = totalWaste - amount

            return result;
        },
        // ============= CALCULATION_FUNCTIONS END =============


        

       
        







        // ============= REFRESH START =============
        async refresh(){
            await this.factoryThreads({product_case: 'raw', factory: this.$route.params.id});
            this.existingIps = this.factory_threads.data;
            this.existingIps.forEach((elem) => {
                elem.name = elem.product.name
                elem.code = elem.product.code
            })
            
        },
        async refresh2(){
            await this.fetchOrders({type: 'material', factory: this.$route.params.id, status: 'in_process'})
            this.existingMatos = this.orders.data;
        },
        // ============= REFRESH START =============









        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.existingProductType = ''
            this.ips = [{id: '', percentage: ''}]
            this.product = {
                amount: 0,
                paid: 0,
                evaporation: ''
            },
            this.existingMatoSearch = '';
            this.existingMato = {product: ''};

            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeOrderDialog(){
            this.clearInputs();
            this.$parent.tookDialog = false;
            this.$refs.formOutsourceOrder.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============
        
        
        
        
        
        // ============= ON_SUBMIT1 START =============
        async onSubmit2(){
            this.$refs.formMatoSelect.validate().then(async(success) => {
                if(success){
                    this.$toast.success('Muvaffaqiyatli tanlandi!');
                    this.productSelectDialog = false
                }
            })
        },
        // ============= ON_SUBMIT1 END =============





        
        
        
        
        
        
   

        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formOutsourceOrder.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============
                    let order_income_extra_products = [];

                    this.ips.forEach(element => {

                        order_income_extra_products.push({
                            product_id: parseInt(element.item.product.id), 
                            percentage: parseFloat(this.product.evaporation),
                            amount: parseFloat(this.productAmount2(element)), 
                            waste_percentage: parseFloat(this.product.evaporation), 
                            waste_amount: parseFloat(this.productAmount(element) - this.productAmount2(element))
                        })

                    });
                    // ============= ADDING_NEW_ORDER START =============
                    let product = await this.postOrderProduct(
                    {
                        created_at: this.date,
                        order_id: parseInt(this.existingMato.id),
                        amount: parseFloat(this.product.amount.toString().replace(/,/g, '')),
                        percentage: parseFloat(this.product.evaporation),
                        product_id: parseInt(this.existingMato.product.id),
                        paid: parseFloat(this.product.paid.toString().replace(/,/g, '')),
                        factory_id: this.$route.params.id,

                        order_income_extra_products: order_income_extra_products,
                        storage_incomes: [
                            {
                                product_id: this.existingMato.product.id,
                                percentage: this.product.evaporation,
                                amount: parseFloat(this.product.amount.toString().replace(/,/g, '')), 
                                waste_percentage: parseFloat(this.product.evaporation), 
                                waste_amount: this.productEvaporation(), 
                                factory_id: this.$route.params.id
                            },
                        ]
                    })


                    
                    if(product.response && product.response.status == 422){
                        if(product.response.data.errors.amount){
                            this.errorMessage = product.response.data.errors.amount[0];
                            this.errorPlace = 'amount';
                        }else if(product.response.data.errors.paid){
                            this.errorMessage = product.response.data.errors.paid[0];
                            this.errorPlace = 'paid';
                        }
                        this.errorStatus = true;
                        this.$toast.error(this.errorMessage);
                    }else if(product.response && product.response.status == 409){
                        if(product.response.data.message){
                            this.errorMessage = product.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        this.$toast.success('Muvaffaqiyatli qo`shildi!');
                        this.closeOrderDialog();

                        if(this.$route.name == 'outsource-mato-toquv-id'){
                            this.$store.commit('main/SET_UPDATE_OUTSOURCE_MATERIAL_OUTCOME', true)
                            this.$store.commit('main/SET_UPDATE_OUTSOURCE_MATERIAL_ORDERS', true)
                            this.$store.commit('main/SET_UPDATE_OUTSOURCE_MATERIAL_WAREHOUSE', true)
                        }else if(this.$route.name == 'outsource-yoqa-toquv-id'){
                            this.$store.commit('main/SET_UPDATE_OUTSOURCE_COLLAR_OUTCOME', true)
                            this.$store.commit('main/SET_UPDATE_OUTSOURCE_COLLAR_ORDERS', true)
                            this.$store.commit('main/SET_UPDATE_OUTSOURCE_COLLAR_WAREHOUSE', true)
                        }

                    }
                    // ============= ADDING_NEW_ORDER END =============


                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============



    }

}
</script>


<style>
.btn-new-white {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 60px;
    border-radius: 10px;
    border: 2px solid #F5F5F5;
    box-shadow: none;
}

</style>
