<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="ipTransferDialog"
        >
            <template>
                <v-card class="ip_add">

                    <div class="ip_add__title">
                        <span class="text-black">Ip o`tkazilmoqda</span>
                        <v-btn icon @click="closeDialog">
                            <img src="@/assets/images/BackButton.svg" alt="BackButton">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOutsourceTransfer" v-slot="{ handleSubmit, invalid }">
                        <form class="ip_add__content" @submit.prevent="handleSubmit(onSubmit)">

                            <div class="mt-3">
                                <span class="text-black ml-1">Sana</span>
                                <v-menu
                                    v-model="dateMenu"
                                    :close-on-content-click="false"
                                    :nudge-right="40"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-text-field
                                                class="mt-2"
                                                placeholder="Tanlang"
                                                v-model="dateFormatted"
                                                readonly
                                                outlined
                                                v-bind="attrs"
                                                v-on="on"
                                                @blur="date = parseDate(dateFormatted)"
                                                :error-messages="errors"
                                            ></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-date-picker
                                        color="blue lighten-1"
                                        v-model="date"
                                        @input="dateMenu = false"
                                        locale="ru-ru"
                                        first-day-of-week="1"
                                        :error-messages="errors"
                                    ></v-date-picker>
                                    </ValidationProvider>
                                </v-menu>
                            </div>


                            <div>
                                <span class="text-black ml-1">Massa (kg)</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'amount']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        v-model="amount" min="1"
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        outlined placeholder="Misol: 1,200 kg" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <span class="text-black ml-1">Qaerga</span>
                            <perfect-scrollbar class="mt-2 mr-1">
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <div class="radioContent mb-5">
                                        <span>Skladga</span>
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="factoryChoosed">
                                            <v-radio value="storage"></v-radio>
                                        </v-radio-group>
                                    </div>

                                    <div class="radioContent mb-5" v-for="(element, index) in factoriesList" :key="index">
                                        <span>{{element.name}}</span>
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="factoryChoosed">
                                            <v-radio :value="element.id"></v-radio>
                                        </v-radio-group>
                                    </div>
                                </ValidationProvider>
                            </perfect-scrollbar>


                            <v-card-actions class="d-flex justify-space-between mt-5">
                                <v-btn
                                    class="backBtn btn"
                                    elevation="0"
                                    @click="closeDialog"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn btn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>

                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: ['ipTransferDialog', 'itemData'],
    mixins: [dateMixin, loaderMixin],
    components: {
        PerfectScrollbar
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            amount: 0,
            factoryChoosed: '',
            factoriesList: [],
            // ============= SECONDARY_DATA =============




            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        amount(newValue, oldValue){
            if(newValue){
                this.clearErrors()
            }
        },
        // ============= OTHER_WATCHERS END =============






    },
    async mounted(){
        await this.refresh()
    },
    computed: {
        ...mapState(
            {
                factories: state => state.categories.factories,
            }
        ),
    },
    methods:{
        ...mapActions(
            {
                transferThread: 'outsource/transferThread',
                fetchFactories: 'categories/fetchFactories',
            }
        ),
        







        // ============= REFRESH START =============
        async refresh(){
            await this.fetchFactories()
            this.factoriesList = this.factories.filter(elem => {
                return elem.factory_category_id == 1 || elem.factory_category_id == 2
            })
        },

        // ============= REFRESH START =============











        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.amount = '';
            this.factoryChoosed = '';
            this.date = '';
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeDialog(){
            this.clearInputs();
            this.$parent.ipTransferDialog = false;
            this.$refs.formOutsourceTransfer.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============
        
        
    
        
        
        
        
        
   

        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formOutsourceTransfer.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= TRANSFERING START =============
                    let resp = await this.transferThread(
                    {
                        factory_id: this.$route.params.id,
                        which_factory_id: this.factoryChoosed,
                        amount: parseFloat(this.amount.toString().replace(/,/g, '')),
                        product_id: parseInt(this.itemData.id),
                        created_at: this.date,
                    })

                    
                    if(resp.response && resp.response.status == 422){
                        if(resp.response.data.errors.amount){
                            this.errorMessage = resp.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(resp.response && resp.response.status == 409){
                        if(resp.response.data.message){
                            this.errorMessage = resp.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        if(resp.success == false){
                            this.errorMessage = resp.message;
                            this.errorPlace = 'amount'
                            this.errorStatus = true
                            this.$toast.error(this.errorMessage);
                        }else{
                            this.$parent.refresh()
                            this.$toast.success('Muvaffaqiyatli o`tkazildi!');
                            this.closeDialog();
                            if(this.$route.name == 'outsource-mato-toquv-id'){
                                this.$store.commit('main/SET_UPDATE_OUTSOURCE_MATERIAL_WAREHOUSE', true)
                            }else if(this.$route.name == 'outsource-yoqa-toquv-id'){
                                this.$store.commit('main/SET_UPDATE_OUTSOURCE_COLLAR_WAREHOUSE', true)
                            }
                        }
                    }
                    // ============= TRANSFERING END =============


                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============



    }

}
</script>


<style>

</style>
