<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="orderDialog"
        >
            <template>
                <v-card class="ip_add">

                    <div class="ip_add__title">
                        <span class="text-black">Buyurtma berilmoqda</span>
                        <v-btn icon @click="closeOrderDialog">
                            <img src="@/assets/images/BackButton.svg" alt="BackButton">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOutsourctOrder" v-slot="{ handleSubmit, invalid }">
                        <form class="ip_add__content" @submit.prevent="handleSubmit(onSubmit)">

                            <div class="mt-3">
                                <span class="text-black ml-1">Sana</span>
                                <v-menu
                                    v-model="dateMenu"
                                    :close-on-content-click="false"
                                    :nudge-right="40"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-text-field
                                                class="mt-2"
                                                placeholder="Tanlang"
                                                v-model="dateFormatted"
                                                readonly
                                                outlined
                                                v-bind="attrs"
                                                v-on="on"
                                                @blur="date = parseDate(dateFormatted)"
                                                :error-messages="errors"
                                            ></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-date-picker
                                        color="blue lighten-1"
                                        v-model="date"
                                        @input="dateMenu = false"
                                        locale="ru-ru"
                                        first-day-of-week="1"
                                        :error-messages="errors"
                                    ></v-date-picker>
                                    </ValidationProvider>
                                </v-menu>
                            </div>


                            <div>
                                <span class="text-black ml-1">Kodni kiriting</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'code']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        type="number" v-model="order.code" min="1"
                                        outlined placeholder="Misol: 12345" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">{{type == 'material' ? 'Mato turi tanlash' : 'Yoqa turi tanlash'}}</span>
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-text-field append-icon="mdi-chevron-right"
                                        @click="productTypeSelectDialog = true"
                                        label="Tanlang" outlined class="mt-5"
                                        :value="existingType.name"
                                        :error-messages="errors">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <div>
                                <span class="text-black ml-1">{{type == 'material' ? 'Mato tanlash' : 'Yoqa tanlash'}}</span>
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-text-field append-icon="mdi-chevron-right"
                                        @click="productSelectDialog = true"
                                        label="Tanlang" outlined class="mt-2"
                                        :value="existingProduct.name"
                                        :error-messages="errors">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">Massa (kg)</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'amount']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        @input="formatPrice(order.amount, 'amount')"
                                        v-model="order.amount" min="1"
                                        outlined placeholder="Misol: 1,200 kg" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <div>
                                <span class="text-black ml-1">KG narxi</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'price']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        @input="formatPrice(order.price, 'price')"
                                        v-model="order.price" min="1"
                                        outlined placeholder="Misol: 240,000 som" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">Umumiy summa</span>
                                <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'total_price']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        @input="formatPrice(order.total_price, 'total_price')"
                                        outlined min="1"
                                        placeholder="Misol: 1,240,000 som"
                                        :value="getTotalPrice"
                                        class="mt-2" v-model="order.total_price">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">Berilgan summa</span>
                                <ValidationProvider :rules="{error: [errorStatus, errorMessage, errorPlace, 'paid']}" v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        onkeypress="return /[0-9,/.]/i.test(event.key)"
                                        @input="formatPrice(order.paid, 'paid')"
                                        outlined min="1"
                                        placeholder="Misol: 1,000,000 som"
                                        class="mt-2" v-model="order.paid">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    class="backBtn btn"
                                    elevation="0"
                                    @click="closeOrderDialog"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn btn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>

                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>


        <v-dialog
            max-width="400"
            persistent
            v-model="productTypeSelectDialog"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title">
                        <span>{{type == 'material' ? 'Mato' : 'Yoqa'}} turini tanlash</span>
                        <v-btn icon @click="productTypeSelectDialog = false">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formProductTypeSelectOrder" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit2)">

                            <!-- <div class="exists mt-5">
                                <span class="exists__title text-black">Bazadagi mato turlari</span>
                                <v-text-field outlined class="exists__input" v-model="productTypeSelect">
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div> -->


                            <perfect-scrollbar class="mt-8 mr-1">
                                <div class="radioContent mb-5" v-for="(element, index) in existingTypes" :key="index">
                                    <span>{{element.name}}</span>
                                    <ValidationProvider 
                                        rules="required"
                                        v-slot="{ errors }">
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="existingType">
                                            <v-radio :value="element"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>


                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>


        <v-dialog
            max-width="400"
            persistent
            v-model="productSelectDialog"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title">
                        <span>{{type == 'material' ? 'Mato tanlash' : 'Yoqa tanlash'}}</span>
                        <v-btn icon @click="productSelectDialog = false">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formProductSelectOrder" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit3)">

                            <div class="exists mt-5">
                                <span class="exists__title text-black">Bazadagi {{type == 'material' ? 'matolar' : 'yoqalar'}}</span>
                                <v-text-field outlined class="exists__input" v-model="productSelect">
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>



                            <perfect-scrollbar class="mr-1" @ps-y-reach-end="addItems">
                                <div class="radioContent mb-5" v-for="(element, index2) in existingProducts" :key="index2">
                                    <span>{{element.name}} - #{{element.code}}</span>
                                    <ValidationProvider 
                                        :rules="new_product == '' ? 'required' : ''"
                                        v-slot="{ errors }">
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="existingProduct">
                                            <v-radio :value="element"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>



                            <div class="mt-4">
                                <span class="text-black ml-1">{{type == 'material' ? 'Yangi mato qoshish' : 'Yangi yoqa qoshish'}}</span>
                                <ValidationProvider
                                :rules="{required: existingProduct == '' ? true : false, error: [errorStatus, errorMessage, errorPlace, 'name']}"
                                    v-slot="{ errors }">
                                    <v-text-field  outlined class="mt-4" :error-messages="errors"
                                        v-model="new_product.name" placeholder="Nomni kiriting">
                                    </v-text-field>
                                </ValidationProvider>

                                <ValidationProvider
                                :rules="{required: existingProduct == '' ? true : false, error: [errorStatus, errorMessage, errorPlace, 'code']}"
                                    v-slot="{ errors }">
                                    <v-text-field  outlined :error-messages="errors"
                                        v-model="new_product.code" placeholder="Kodni kiriting">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: ['orderDialog', 'type'],
    mixins: [dateMixin, loaderMixin],
    components: {
        PerfectScrollbar
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            page: 1,
            last_page: 2,
            pageCount: 0,

            productTypeSelectDialog: false,
            productSelectDialog: false,
            productTypeSelect: '',
            productSelect: '',
            order: {
                name: '',
                code: '',
                amount: 0,
                price: 0,
                paid: 0,
                total_price: 0,
            },
            new_product: {
                name: '',
                code: ''
            },
            existingTypes: [],
            existingProducts: [],
            existingType: '',
            existingProduct: '',
            // ============= SECONDARY_DATA =============






            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        // ============= CLEAR_ERRORS START =============
        'new_product.name': {
            handler(newValue){
                if(newValue != ''){
                    this.clearErrors()
                    this.existingProduct = ''
                }
            },deep: true
        },
        'new_product.code': {
            handler(newValue){
                if(newValue != ''){
                    this.clearErrors()
                    this.existingProduct = ''
                }
            },deep: true
        },
        'order': {
            handler(newValue){
                this.clearErrors()
            },deep: true
        },
        existingProduct(newValue, oldValue){
            if(newValue != ''){
                this.clearErrors()
                this.new_product.name = ''
                this.new_product.code = ''
            }
        },
        // ============= CLEAR_ERRORS END =============









        // ============= OTHER_WATCHERS START =============
        async productSelect(newValue, oldValue){
            this.page = 1;
            if(newValue || newValue == ''){
                await this.refresh2();
            }
        }, 
        // ============= OTHER_WATCHERS END =============





    },
    async mounted(){
        await this.refresh()
        await this.refresh2()
    },
    computed: {
        ...mapState(
            {
                productCodesFields: state => state.code.productCodesFields,
                productCategories: state => state.categories.productCategories
            }
        ),



        // ============= CALCULATE_TOTAL_PRICE START =============
        getTotalPrice() {
            let price = this.order.price.toString().replace(/,/g, '');
            let amount = this.order.amount.toString().replace(/,/g, '');
            price = parseFloat(price)
            amount = parseFloat(amount)

            let result = price * amount
            
            return  this.order.total_price = new Intl.NumberFormat('en-US').format(result)
        }
        // ============= CALCULATE_TOTAL_PRICE END =============



    },
    methods:{
        ...mapActions(
            {
                postMatoOrder: 'outsource/postMatoOrder',
                fetchProductCodesFiltered: 'code/fetchProductCodesFiltered',
                saveProductCode: 'code/saveProductCode',
                fetchCategories: 'categories/fetchCategories',
            }
        ),
        // ============= REFRESH START =============
        async refresh(){
            await this.fetchCategories()
            if(this.type == 'material'){
                this.existingTypes = this.productCategories.filter((elem) => elem.parent_id == 2)
            }else{
                this.existingTypes = this.productCategories.filter((elem) => elem.parent_id == 3)
            }
        },
        // ============= REFRESH END =============




        // ============= REFRESH START =============
        async refresh2(){
            this.page = 1;
            
            clearTimeout(this._timerId)

            this._timerId = setTimeout(async () => {
                await this.fetchProductCodesFiltered({page: this.page, type_id: this.existingType.id, search: this.productSelect})

                this.page = this.productCodesFields.current_page;
                this.last_page = this.productCodesFields.last_page;
                this.pageCount = Math.ceil(this.productCodesFields.total / 20);
                
                this.existingProducts = this.productCodesFields.data;
            }, 500)
        },
        // ============= REFRESH START =============







        // ============= ADD_ITEMS START =============
        async addItems(){
            if(this.page < this.last_page){
                this.page = this.page + 1;

                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                    await this.fetchProductCodesFiltered({page: this.page, type_id: this.existingType.id, search: this.productSelect})

                    this.page = this.productCodesFields.current_page;
                    this.pageCount = Math.ceil(this.productCodesFields.total / 20);

                    this.existingProducts = this.existingProducts.concat(this.productCodesFields.data);
                }, 500)
            }
        },
        // ============= ADD_ITEMS END =============













        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){

                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);

                if(input == 'amount'){
                    this.order.amount = currency
                }else if(input == 'price'){
                    this.order.price = currency
                }else if(input == 'total_price'){
                    this.order.total_price = currency
                }else if(input == 'paid'){
                    this.order.paid = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============








        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.productSelect= '';
            this.order = { name:'', code: '', amount: 0, price: 0, paid: 0, total_price: 0 };
            this.new_product= { name: '', code: '' };
            this.existingProduct = '';

            this.date = '';
            this.clearErrors();
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeOrderDialog(){
            this.clearInputs();
            this.$parent.orderDialog = false;
            this.$refs.formOutsourctOrder.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============
        
        
        


        // ============= ON_SUBMIT2 START =============
        async onSubmit2(){
            this.$refs.formProductTypeSelectOrder.validate().then(async(success) => {
                if(success){
                    this.productTypeSelectDialog = false
                    
                    await this.refresh2()
                }
            })
        },
        // ============= ON_SUBMIT2 END =============







        // ============= ON_SUBMIT3 START =============
        async onSubmit3(){
            this.$refs.formProductSelectOrder.validate().then(async(success) => {
                if(success){
                    if(this.new_product.name != '' && this.new_product.code != ''){
                        let resp = await this.saveProductCode({name: this.new_product.name, 
                            code: parseFloat(this.new_product.code), product_category_id: this.existingType.id});
                        await this.refresh()


                        if(resp.response && resp.response.status == 422){
                            if(resp.response.data.errors.code){
                                this.errorMessage = resp.response.data.errors.code[0];
                                this.errorPlace = 'code'
                            }else if(resp.response.data.errors.name){
                                this.errorMessage = resp.response.data.errors.name[0];
                                this.errorPlace = 'name'
                            }
                            this.errorStatus = true
                            this.$toast.error(this.errorMessage);
                        }else if(resp.response && resp.response.status == 409){
                            if(resp.response.data.message){
                                this.errorMessage = resp.response.data.message;
                            }
                            this.$toast.error(this.errorMessage);
                        }else{
                            this.existingProduct = resp
                            this.$toast.success('Muvaffaqiyatli qo`shildi!');
                            this.productSelectDialog = false
                        }
                    }else{
                        this.productSelectDialog = false
                    }
                }
            })
        },
        // ============= ON_SUBMIT3 END =============
        
        
        
        
        
        
        
   

        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formOutsourctOrder.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= ADDING_NEW_ORDER START =============
                    let order = await this.postMatoOrder(
                    {
                        created_at: this.date,
                        code: parseFloat(this.order.code.toString().replace(/,/g, '')),
                        product_id: parseInt(this.existingProduct.id),
                        amount: parseFloat(this.order.amount.toString().replace(/,/g, '')),
                        price: parseFloat(this.order.price.toString().replace(/,/g, '')),
                        total_price: parseFloat(this.order.total_price.toString().replace(/,/g, '')),
                        paid: parseFloat(this.order.paid.toString().replace(/,/g, '')),
                        factory_id: parseInt(this.$route.params.id),
                        type: this.type
                    })

                    
                    if(order.response && order.response.status == 422){
                        if(order.response.data.errors.code){
                            this.errorMessage = order.response.data.errors.code[0];
                            this.errorPlace = 'code'
                        }else if(order.response.data.errors.amount){
                            this.errorMessage = order.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }else if(order.response.data.errors.price){
                            this.errorMessage = order.response.data.errors.price[0];
                            this.errorPlace = 'price'
                        }else if(order.response.data.errors.total_price){
                            this.errorMessage = order.response.data.errors.total_price[0];
                            this.errorPlace = 'total_price'
                        }else if(order.response.data.errors.paid){
                            this.errorMessage = order.response.data.errors.paid[0];
                            this.errorPlace = 'paid'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(order.response && order.response.status == 409){
                        if(order.response.data.message){
                            this.errorMessage = order.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        this.$toast.success('Muvaffaqiyatli qo`shildi!');
                        this.closeOrderDialog();
                        this.$store.commit('main/SET_UPDATE_OUTSOURCE_MATERIAL_ORDERS', true)
                        this.$store.commit('main/SET_UPDATE_OUTSOURCE_COLLAR_ORDERS', true)

                    }
                    // ============= ADDING_NEW_ORDER END =============


                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============



    }

}
</script>


<style>

</style>
